<template>
	<div class="goods-item" @click="goodsItemClick">
		<img :src="showImage" alt @load="imgload" />
		<div class="goodsinfo">
			<p>{{goodsitem.title}}</p>
			<span class="price">{{goodsitem.price}}</span>
			<span class="collect">{{goodsitem.cfav}}</span>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		goodsitem: {
			type: Object,
			default() {
				return {};
			},
		},
	},
	methods: {
		imgload() {
			if (!this.$route.path.indexOf('/home')) {
				this.$bus.$emit('itemImgLoad');
			} else if (!this.$route.path.indexOf('/detail')) {
				this.$bus.$emit('detailImgLoad');
			}
		},
		goodsItemClick() {
			this.$router.push('/detail/' + this.goodsitem.iid);
		},
	},
	computed: {
		showImage() {
			return this.goodsitem.image || this.goodsitem.show.img;
		},
	},
};
</script>

<style>
.goods-item {
	position: relative;
	width: 46%;
	padding-bottom: 10%;
	margin-bottom: 3%;
}
.goods-item .goodsinfo {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	overflow: hidden;
}
.goods-item p {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}
.goods-item .price {
	color: #e6335f;
	margin-right: 16px;
}
.goods-item .collect {
	position: relative;
}
.goods-item .collect::after {
	content: '';
	position: absolute;
	width: 14px;
	height: 14px;
	left: -14px;
	top: 0px;
	background: url('../../../assets/img/common/collect.svg') no-repeat 0 0/14px
		14px;
}

.goods-item img {
	border-radius: 5%;

	width: 100%;
}
</style>